<template>
  <div class="container register-wrap">
    <img class="bg" src="../../images/register/bg.png" alt="">
    <div class="wrap-center">
      <div class="title">创建APP</div>
      <el-form label-width="100px" class="dataForm">
        <el-form-item label="APP中文名：">
          <el-input placeholder="请输入APP中文名"></el-input>
        </el-form-item>
        <el-form-item label="APP英文名：">
          <el-input placeholder="请输入APP英文名"></el-input>
        </el-form-item>
        <el-form-item label="APP描述：">
          <el-input placeholder="请输入APP描述" type="textarea"></el-input>
        </el-form-item>
       
        
      </el-form>
      <div class="prompt">
        <div>提示：</div>
        <div>APP命名建议为贵公司名称与业务的组合，不能填写纯数字、手机号或特殊符号
        </div>
      </div>
      <button class="btn">提交</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      
    }
  },
  methods:{

  }
}
</script>


<style lang="less" scoped>
.register-wrap {
  position: relative;
  .bg {
    width: 100%;
    display: block;
    min-height: calc(100vh - 66px - 100px);
  }
  .wrap-center {
    width: 594px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background: #fff;
    padding: 50px 58px 55px 58px;
    box-sizing: border-box;
    .title {
      color: #1A1A1A;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      margin-bottom: 45px;
    }
    .dataForm {
      width: 360px;
      display: block;
      margin: 0 auto;
      /deep/ .el-form-item__label{
        color: #1F1E1E;
        font-weight: 500;
      }
    }
    .prompt {
      width: 360px;
      margin: 0 auto 38px auto;
      color: #8E8E8E;
      font-size: 14px;
      display: flex;
      
      > div:first-child{
         flex-shrink: 0;
      }
    }
    .btn {
      width: 360px;
      height: 40px;
      line-height: 40px;
      text-align: 40px;
      background-color: #B90909;
      color: #fff;
      font-size: 14px;
      font-weight: 50px;
      border: 0;
      border-radius: 3px;
      display: block;
      margin: 0 auto;
    }
  }
}
</style>


